.col_slide_1 {
  width: 460px;
  height: 536px;


  .swiper-container {
    margin-bottom: 20px;
  }

  .swiper-button-prev:after,
  .swiper-button-next:after {
    color: #ffffff;
    font-size: 20px;
  }

  .swiper-pagination-bullet {
    --swiper-theme-color: #ffffff;
  }
}

.col_title {
  width: 419px;
  height: 456px;
  background-color: blueviolet;
  margin-left: 85px;
}

.col_button {
  width: 71px;
  height: 56px;

  .btn {
    width: 180px;
    height: 56px;
    background-color: #5956E9;
    border-radius: 10px;
    color: #ffffff;
    font-size: 18px;
    font-weight: bold;
    margin: 35px;
    font-family: "Jost", sans-serif;

    &:hover {
      box-shadow: 0 0 10px 1px #5956E9;
    }
  }
}

.services {
  width: 100%;
  height: 313px;
  background-color: blue;
  margin-top: 100px;
}

.col_slide_2 {
  width: 100%;
  height: 204px;
  margin: 42px 0 0 0;

  h2 {
    font-size: 32px;
    font-family: "Jost", sans-serif;
    font-weight: 600;
  }

  .slide__2 {
    width: 100%;
    height: 116px;
    margin: 42px 0 0 0;
     .swiper-button-prev:after,
     .swiper-button-next:after {
      color: #000000;
      font-size: 20px;
    }
    h5 {
      font-size: 14px;
      font-family: "Jost", sans-serif;
      font-weight: bold;
      margin: 5px 0 5px 0;
    }
    p {
      font-size: 12px;
      font-family: "Jost", sans-serif;
      color: #91959B;
    }
  }
}

.row_fluid {
  width: 100%;
  height: 360px;
  background-color: #97979797;
  margin-top: 63px;
}

